	module.exports = {"data":{"markdownRemark":{"html":"<img src='/static/images/idistract_500px.jpg' alt='iDistract' />\n<p>I've never seen gadget hype reach the levels that have been achieved by the iPhone. And I've never been so caught up in it myself. After visiting <a href=\"http://www.eyesondarfur.org/\">eyesondarfur.org</a> I've decided that I'm giving my iPhone budget to <a href=\"http://www.eyesondarfur.org/\">Amnesty International</a>: $50 a month over the next year.</p>\n<p>In the <a href=\"http://en.wikipedia.org/wiki/Culture_jamming\">culture jamming</a> spirit I spliced an iPhone ad into one of the arresting images from the book <a href=\"https://secure3.ctsg.com/amnestyusa/store/viewProduct.asp?cookietest=1&From=%2Famnestyusa%2Fstore%2FviewProduct%2Easp&Product=600&CategoryName=New+Products&CategoryId=11\">Darfur: Twenty Years of War and Genocide in Sudan</a>. I'm not anti-iPhone in particular: I just want to remind folks (<em>especially</em> myself) that there are more important things to focus on than the gadgets being thrust in your face. </p>\n<p>If you have the luxury of a budget for consumer electronics, why not consider putting just a percentage of it toward something like an anti-genocide campaign? </p>\n<p>[image: The Kalma Camp, Darfur, by Pep Bonet/Panos Pictures and the iPhone by Apple Inc. Used without permission: Please don't sue me.] </p>","frontmatter":{"created_at":"June 15, 2007","path":"/2007/06/15/genocide-vs-gadgets","title":"Genocide vs. Gadgets","tags":["darfur","design","culturejamming","iphone","designfailure","failedsociety"]}}},"pathContext":{}}

